.search_box {
  position: relative;
}

.search_box:before,
.search_box .before {
  /*.before为ie6下用到*/
  content: '';
  /*伪类下的属性*/
  display: block;
  position: absolute;
  top: -0.4rem;
  left: 50%;
  width: 0;
  height: 0;
  overflow: hidden;
  font-size: 0;
  /*是因为, 虽然宽高度为0, 但在IE6下会具有默认的 */
  line-height: 0;
  /* 字体大小和行高, 导致盒子呈现被撑开的长矩形 */
  border: 0.186667rem;
  border-style: dashed dashed solid dashed;
  border-color: transparent transparent #C5C5C5 transparent;
}

.search_box:after,
.search_box .after {
  /*.after为ie6下用到*/
  content: '';
  /*伪类下的属性*/
  display: block;
  width: 0;
  height: 0;
  overflow: hidden;
  font-size: 0;
  /*是因为, 虽然宽高度为0, 但在IE6下会具有默认的 */
  line-height: 0;
  /* 字体大小和行高, 导致盒子呈现被撑开的长矩形 */
  border: 0.186667rem;
  border-style: dashed dashed solid dashed;
  border-color: transparent transparent #fff transparent;
  position: absolute;
  left: 50%;
  top: -0.373333rem;
}

